<template>
  <div class="">
    <div class="header">
      <div class="title">
        <span class="title_txt">{{allData.goods.title}}</span>
      </div>
      <div class="head_img">
        <img class="head_img_c" v-preview="host + allData.goods.image" v-bind:src="host + allData.goods.image" alt="">
        <!-- <img class="head_img_c" v-preview="allData.goods.image" v-bind:src=`${host}${allData.goods.image}` alt=""> -->

      </div>
      <div class="head_meg">
        <div class="">
          <span class="meg_one">  {{allData.goods.crop}}</span>
          <span>  {{allData.goods.variety}}</span>
        </div>
        <div class="">
          <span class="meg_one">  {{allData.goods.spec}}</span>
          <span>  {{allData.goods.brand}}</span>
        </div>
      </div>
      <div class="">
        <span class="img_title"></span>

        <div class="img_list"  style="padding: 0 24px;">
          <img v-for='item in allData.goods.images.split(",")' v-preview="item" class="img_item" :src="host + item" alt="">
        </div>
      </div>
    </div>
    <div class="farming">
      <img class="bg_title" src="../assets/img/farming.png" alt="">
      <div class="farming_list" v-for='item in allData.farm'>
        <img class="farming_img" src="../assets/img/fm.png" alt="">
        <span style="color: #118C3E; font-size: 12px;">{{item.date}}</span>
        <span style="font-size: 12px;">{{item.name}}</span>
        <div class="" style="border-bottom: 1px dashed #E8E8E8">
          <span style="font-size: 12px;">{{item.describe}}</span>
          <div class="farming_img_list">
            <img v-for='img in item.images.split(",")' v-preview="img" class="farming_img_item" :src="host + img" alt="">
          </div>
        </div>
      </div>
    </div>
    <div class="farming">
      <img class="bg_title" src="../assets/img/order.png" alt="">
      <div class="" style="padding: 0 24px">
        <div class="">
          <span style="font-size: 12px;"> {{allData.order.company}} </span></br>
          <span style="font-size: 12px;"> {{allData.order.date}} </span>
        </div>
        <span style="color: #118C3E; font-size: 12px;"></span>
        <div class="car_list" v-for='(item, index) in JSON.parse(allData.order.cars)'>
          <img class="farming_img" src="../assets/img/car.png" alt="">
          <span style="font-size: 12px;">{{index + 1 }}</span>
          <div class="">
            <span class="car_meg"> {{item.driver}}</span>
            <span class="car_meg"> {{item.num}}</span></br>
            <span class="car_meg"> {{item.licence}}</span>
          </div>
        </div>
      </div>
    </div>
    <div class="farming">
      <img class="bg_title" src="../assets/img/detail.png" alt="">
      <div >
        <div style="padding: 0 24px" class="">
          <span style="font-size: 14px; color: #333">{{allData.base.name}}</span></br>
          <span style="font-size: 12px; color: #666"> {{allData.base.type}}</span>
          <span style="font-size: 12px; color: #666"> {{allData.base.hesd}}  {{allData.base.phone}}</span>
        </div>
        <div class="detail_list">
          <div class="car_item border_right">
            <span style="color: #16B751;font-size: 12px"></span></br>
            <span style="color: #666;font-size: 18px">{{allData.base.areaTotal}} <span style="font-size: 10px"></span></span>
          </div>
          <div class="car_item border_right">
            <span style="color: #16B751;font-size: 12px"></span></br>
            <span style="color: #666;font-size: 18px">{{allData.base.productionTotal}} <span style="font-size: 10px"></span></span>
          </div>
          <div class="car_item border_right">
            <span style="color: #16B751;font-size: 12px"></span></br>
            <span style="color: #666;font-size: 18px">{{allData.base.personTotal}}<span style="font-size: 10px"></span></span>
          </div>
          <div class="car_item">
            <span style="color: #16B751;font-size: 12px"></span></br>
            <span style="color: #666;font-size: 18px">{{allData.base.poorTotal}} <span style="font-size: 10px"></span></span>
          </div>
        </div>
        <div class="detail_title">
          <span class="line_j"></span>
          <span style="color: #16B751;font-size: 12px"></span>
          <span class="line_j"></span>
        </div>
        <div class="img_list" style="padding: 0 10px;">
          <img class="detail_img" v-preview="img" v-for='img in allData.base.images.split(",")' :src="host + img" alt="">
        </div>
        <div :class="{'detail_text':true,'onShow': showTxt}" style="padding: 0 10px;">
          <span>{{allData.base.summary}}</span>
        </div>
        <div class="up_or_down" v-on:click='showDetail'>
          <img v-if='showTxt' class="up_img" src="../assets/img/down.png" alt="">
          <img v-else class="up_img" src="../assets/img/up.png" alt="">
          <!-- <span style="margin-left: 3px;color: #999; font-size: 12px"></span> -->
        </div>
        <div class="detail_title">
          <span class="line_j"></span>
          <span style="color: #16B751;font-size: 12px"></span>
          <span class="line_j"></span>
        </div>
        <div class="people_list" v-for='item in poorlist'>
          <span class="person_wid" style="width: 40%; display: inline-block">{{item.name}}</span>
          <span class="person_wid" style="width: 40%;display: inline-block">{{item.number}}</span>
          <span class="person_wid" v-if="item.poor" style="color: #FF6203"></span>
        </div>
        <div class="up_or_down" v-if='JSON.parse(allData.base.persons).length > 1' >
          <img v-if='showPoor' class="up_img" src="../assets/img/down.png" alt="" v-on:click='showList'>
          <img v-else class="up_img" src="../assets/img/up.png" alt="" v-on:click='offList'>

          <span v-if='showPoor' style="margin-left: 3px;color: #999; font-size: 12px" v-on:click='showList'></span>
          <span v-else style="margin-left: 3px;color: #999; font-size: 12px" v-on:click='offList'></span>

        </div>
      </div>
    </div>
    <div class="farming">
      <img class="bg_title" src="../assets/img/meg.png" alt="">
      <div class="" style="padding: 0 24px 10px">
        <div class="" style="text-align: left;line-height:20px">
          <span style="font-size: 12px; width: 70px; color:#666; display: inline-block"> </span>
          <span style="font-size: 12px;color:#333;">{{allData.monitor.title}}</span></br>

          <span style="font-size: 12px; width: 70px; color:#666; display: inline-block"></span>
          <span style="font-size: 12px;color:#333;"> {{allData.monitor.type}} </span></br>

          <span style="font-size: 12px; width: 70px; color:#666; display: inline-block"> </span>
          <span style="font-size: 12px;color:#333;"> {{allData.monitor.number}} </span></br>

          <span style="font-size: 12px; width: 70px; color:#666; display: inline-block">  </span>
          <span style="font-size: 12px;color:#333;"> {{allData.monitor.person}} </span></br>

          <span style="font-size: 12px; width: 70px; color:#666; display: inline-block">  </span>
          <span style="font-size: 12px;color:#16B751;"> {{allData.monitor.result}} </span></br>

        </div>
      </div>
      <div class="farming_img_list" style="padding: 0 24px">
        <img v-for='item in allData.monitor.images.split(",")' v-preview="item" class="farming_img_item" :src="host + item" alt="">
      </div>
    </div>
    <div class="farming">
      <img class="bg_title" src="../assets/img/cardId.png" alt="">
      <div class="farming_img_list" style="padding: 0 24px">
        <div class="" v-for='item in allData.certificate' style="display: inline-block; vertical-align: top;">
          <img  class="detail_img" :src="host + item.images" v-preview="host + item.images" alt=""></br>
          <span class="detail_txt">{{item.name}}</span>
        </div>
      </div>
    </div>
    <div class="logo">
      <img class="logo_img" src="../assets/img/logo.png" alt="">
    </div>
  </div>

</template>

<script>
import fancyBox from 'vue-fancybox';

export default {
  data() {
    return {
      // host: 'http://127.0.0.1:7001',
      host: 'http://118.126.104.19:7001',
      allData: {},
      showTxt: true,
      showPoor: true,
      poorlist: [],
    }
  },

  created() {
    this.readlist()
  },
  methods: {
    readlist() {
      // 
      var self = this
      var id = this.$route.params.id
      console.log(id,'iiiiiiiiiiiii');
      this.axios.get(`${this.host}/phoneuuyr/${id}`).then((res) => {
        // console.log(res.data.data,'res.data.data');
        self.allData = res.data.data
        self.poorlist = JSON.parse(res.data.data.base.persons).slice(0, 1)
        console.log(self.allData.goods.title,'respresp');
      });
    },
    showDetail() {
      this.showTxt = !this.showTxt
    },
    showList() {
      this.poorlist = JSON.parse(this.allData.base.persons)
      console.log(this.poorlist,'this.poorlist');
      this.showPoor = false
    },
    offList() {
      this.showPoor = true
      this.poorlist = JSON.parse(this.allData.base.persons).slice(0, 1)
    },
    open (e,img) {
      console.log(img,'img');
      var imageList = [{ url: img }]
      fancyBox(e.target, imageList);
    }
  }
}
</script>

<style lang="less" scoped>

  @root-px: 75 * 1rem;

  .px2rem(@name, @px) {
    @{name}: @px / @root-px;
  }
.header {
  background: url('../assets/img/bg.png');
  width: 100%;
  height: 600px;
  background-size: 100% 100%;
  margin: 0;
}
.title {
  color: rgba(115, 62, 15, 1);
  font-size: 16px;
  width: 300px;
  margin: 0 auto;
  padding-top: 16px;
  text-align: center;
}
.title_txt {
  width: 200px;
  display: inline-block;
}
.head_img {
  height: 140px;
  padding: 0 24px;
  margin-top: 16px;
}
.head_img_c {
  width: 100%;
  height: 100%;
  border: 3px solid #fff;
}
.head_meg {
  padding: 16px 24px 6px;
  color: #733E0F;
  left: 56px;
  line-height: 20px;
  font-size: 12px;
}
.meg_one {
  width: 50%;
  display: inline-block;
}
.img_title {
  padding: 0 24px;
  color: #733E0F;
  line-height: 20px;
  font-size: 12px;
}
.img_list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.img_item {
  // width: 90px;
  // height: 60px;
  .px2rem(width,450);
  .px2rem(height,300);

  margin-top: 4px;
  border: 3px solid #fff;
  // flex-grow: 1;
  margin-right: 4px;
}
.bg_title {
  width: 200px;
  height: 30px;
  padding: 16px 0;
}
.farming_list {
  padding: 0 24px;
}
.farming_img {
  width: 12px;
  height: 12px;
}
.farming_img_list{
  padding: 7px 0;
}
.farming_img_item {
  width: 48px;
  height: 48px;
  margin-right: 4px;

}
.car_meg {
  width: 45%;
  font-size: 12px;
  color: #666;
  display: inline-block;
}
.car_list {
  /* width: 343px; */
  box-sizing: border-box;
  padding: 10px 0px;
  border-bottom: 2px solid rgba(232, 232, 232, 1);
}
.car_item {
  text-align: center;
  display: inline-block;
  flex-grow: 1
}
.detail_list {
  display: flex;
  /* flex-wrap: wrap; */
  justify-content: space-between;
  padding: 10px 0;
}
.border_right {
  border-right: 2px solid rgba(232, 232, 232, 1);

}
.detail_title {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-content: center;
  padding: 0 10px 7px;
}
.line_j {
  width: 130px;
  display: inline-block;
  background-color: #EFF8F6;
  height: 2px;
  margin-top: 8px;
}
.detail_img {
  width: 75px;
  height: 60px;
  margin-right: 4px;
  margin-top: 4px;

  flex-grow: 1
}
.detail_text {
  // width: 359px;
  line-height: 20px;
  color: rgba(51, 51, 51, 1);
  font-size: 12px;
  text-align: justify;
}
.detail_txt {
  display: inline-block;
  font-size: 12px;
  width: 80px;
  color: #333;
}
.onShow {
  overflow:hidden;
  text-overflow:ellipsis;
  display:-webkit-box;
  -webkit-box-orient:vertical;
  -webkit-line-clamp:2;
  height: 40px;
}
.up_or_down {
  text-align: center;
  line-height: 30px;
}
.up_img {
  width: 12px;
  height: 6px;
}
.people_list {
  line-height: 20px;
  color: rgba(51, 51, 51, 1);
  font-size: 12px;
  /* justify-content: space-between;
  align-content: center;
  display: flex; */
}
.person_wid {
  /* flex-grow: 1; */
  text-align: center;

}
.logo {
  padding: 24px;
  text-align: center;
}
.logo_img {
  width: 130px;
  height: 50px;
}
</style>



